import Vue from 'vue';

class ErrorNotifier {

    public static notify() {
        Vue.prototype.$notify({
            classes: 'vue-notification-error',
            title: '',
            text: '    ',
        });
    }

    public static notifyWithCustomMessage(message: string) {
        Vue.prototype.$notify({
            classes: 'vue-notification-error',
            title: '',
            text: message,
        });
    }
}

export default ErrorNotifier;
